
import { defineComponent } from 'vue';
import {
  ContentBlock,
  CopyToClipboard,
  SiteSelector,
  SiteRef,
  Matomo,
  MatomoUrl,
} from 'CoreHome';
import { Field } from 'CorePluginsAdmin';

interface UrlGeneratorState {
  site: SiteRef,
  rows: number,
  cols: number,
  font: string,
  fontsize: number,
  fontcolor: string,
  date: string,
  period: string,
  showcode: boolean,
  hideflag: boolean,
}

export default defineComponent({
  props: {
    title: String,
    fonts: Object,
    periodsNames: Object,
  },
  data(): UrlGeneratorState {
    return {
      site: {
        id: MatomoUrl.parsed.value.idSite as string,
        name: Matomo.helper.htmlDecode(Matomo.siteName),
      },
      rows: 5,
      cols: 2,
      font: '',
      fontsize: 12,
      fontcolor: '0,0,0',
      date: MatomoUrl.parsed.value.date as string,
      period: MatomoUrl.parsed.value.period as string,
      showcode: false,
      hideflag: false,
    };
  },
  components: {
    Field,
    ContentBlock,
    SiteSelector,
  },
  directives: {
    CopyToClipboard,
  },
  computed: {
    flagCounterUrl() {
      const prefix = window.location.href.split('?')[0];
      return `${prefix}?${MatomoUrl.stringify({
        ...MatomoUrl.urlParsed.value,
        action: 'image',
        idSite: this.site.id,
        rows: this.rows,
        cols: this.cols,
        font: this.font,
        fontsize: this.fontsize,
        fontcolor: this.fontcolor,
        date: this.date,
        period: this.period,
        showcode: this.showcode ? 1 : 0,
        showflag: this.hideflag ? 0 : 1,
      })}`;
    },
  },
});
